@import-normalize;

* {
	box-sizing: border-box;
}

html body {
	font-size: 16px;
	padding: 0;
	margin: 0;
	width: 100vw;
	height: 100vh; /* Fallback for browsers that do not support Custom Properties */
	height: calc(var(--vh, 1vh) * 100);
	font-family: 'Open Sans', sans-serif;
}

#root {
	width: 100%;
	height: 100%;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
}
